.gpt__whatgpt {
	display: flex;
	flex-direction: column;
	padding: 2rem;
	background: var(--color-footer);

	background: -moz-radial-gradient(
		circle at 30% -100%,
		#042c54 25%,
		rgba(4, 44, 84, 1) 85%,
		rgba(27, 120, 222, 1) 100%
	);

	background: -webkit-radial-gradient(
		circle at 30% -100%,
		#042c54 25%,
		rgba(4, 44, 84, 1) 85%,
		rgba(27, 120, 222, 1) 100%
	);

	background: -o-radial-gradient(
		circle at 30% -100%,
		#042c54 25%,
		rgba(4, 44, 84, 1) 85%,
		rgba(27, 120, 222, 1) 100%
	);

	background: -ms-radial-gradient(
		circle at 30% -100%,
		#042c54 25%,
		rgba(4, 44, 84, 1) 85%,
		rgba(27, 120, 222, 1) 100%
	);

	background: radial-gradient(
		circle at 30% -100%,
		#042c54 25%,
		rgba(4, 44, 84, 1) 85%,
		rgba(27, 120, 222, 1) 100%
	);

	filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.gpt__whatgpt-feature {
	display: flex;
}

.gpt__whatgpt-feature .gpt__features-container__feature {
	margin: 0;
}

.gpt__whatgpt-feature .gpt__features-container__feature-text {
	max-width: 700px;
}

.gpt__whatgpt-heading {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin: 4rem 0 2rem;
}

.gpt__whatgpt-heading h1 {
	font-family: var(--font-family);
	font-weight: 800;
	font-size: 34px;
	line-height: 45px;
	max-width: 510px;
}

.gpt__whatgpt-heading p {
	font-family: var(--font-family);
	font-weight: 500;
	font-size: 16px;
	line-height: 30px;
	color: var(--color-subtext);
	cursor: pointer;
}

.gpt__whatgpt-container {
	display: flex;
	flex-wrap: wrap;
	margin-top: 2rem;
}

.gpt__whatgpt-container .gpt__features-container__feature {
	flex: 1;
	margin: 1rem;
	min-width: 210px;
	display: unset;
	flex-direction: column;
}

.gpt__whatgpt-container .gpt__features-container__feature-text {
	margin-top: 0.5rem;
}

@media screen and (max-width: 850px) {
	.gpt__whatgpt-heading {
		flex-direction: column;
		align-items: flex-start;
		margin-bottom: 0rem;
	}

	.gpt__whatgpt-heading p {
		margin-top: 1rem;
	}
}

@media screen and (max-width: 650px) {
	.gpt__whatgpt-feature .gpt__features-container__feature {
		flex-direction: column;
	}

	.gpt__whatgpt-feature .gpt__features-container__feature-text {
		margin-top: 0.5rem;
	}

	.gpt__whatgpt-heading h1 {
		font-size: 28px;
		line-height: 40px;
	}
}

@media screen and (max-width: 350px) {
	.gpt__whatgpt-container .gpt__features-container__feature {
		margin: 1rem 0;
		min-width: 100%;
	}
}
